import React from 'react';
import './App.css';
import App from './App'
export default function TodoComponent({ todo,toggleTodo }) {
    function handleTodoClick() {
        toggleTodo(todo.id)
    }



    return (
        <div className='div_co'>
            <div className='div_todo'>
                <label id='label'>

                    <input type="checkbox" value='1' checked={todo.complete} onChange={handleTodoClick} />
                    
                    {todo.name}
                    
                    
                    
                </label>
            </div>
        </div>
    )
}
